import * as React from 'react';
import Box from '@mui/material/Box';
import Button from '@mui/material/Button';
import Grid from '@mui/material/Grid';
import TextField from '@mui/material/TextField';
import Typography from '@mui/material/Typography';
import { Layout } from '../components/layout';
import { Seo } from '../components/seo';
import { Navigation } from '../components/navigation';

const Page = () => {
  const googleFormUrl =
    'https://docs.google.com/forms/u/0/d/e/1FAIpQLSdW7RyFa34m_hWS-dIT1R8Z_6FuneOTCKY69gm6_ympNGyzMA/formResponse';

  const clearForms = () => ({
    name: '',
    email: '',
    inquiry: ''
  });

  const [disabled, setDisabled] = React.useState(true);
  const [isSubmit, setIsSubmit] = React.useState(false);
  const [forms, setForms] = React.useState(clearForms());
  const [errors, setErrors] = React.useState(clearForms());
  const refInputs = {
    name: React.useRef(),
    email: React.useRef(),
    inquiry: React.useRef()
  };

  const validate = () => {
    const _errors = ['name', 'email', 'inquiry'].reduce((obj, id) => {
      const { current } = refInputs[id];
      const msg = current.validationMessage;
      obj[id] = msg;

      return obj;
    }, {});

    const isError = Object.keys(_errors).some(id => _errors[id] !== '');

    setErrors(_errors);
    setDisabled(isError);

    return !isError;
  };

  return (
    <Layout>
      <Navigation />
      <Box py={10}>
        <Grid container justifyContent='center' spacing={4}>
          <Grid item md={6} sm={8} xs={10}>
            {isSubmit ? (
              <Box>
                <Typography align='center' variant='h5' py={10}>
                  Thank you for your inquiry.
                </Typography>
              </Box>
            ) : (
              <Box>
                <form
                  action={googleFormUrl}
                  method='post'
                  target='onSubmitIframe'
                >
                  {[
                    { id: 'name', name: 'entry.232082789', label: 'Name' },
                    {
                      id: 'email',
                      name: 'entry.1587906256',
                      label: 'Mail address',
                      type: 'email'
                    },
                    {
                      id: 'inquiry',
                      name: 'entry.1955861632',
                      label: 'Message',
                      multiline: true,
                      minRows: 3
                    }
                  ].map(
                    ({
                      id,
                      name,
                      label,
                      type = 'text',
                      multiline = false,
                      minRows = 1
                    }) => (
                      <TextField
                        key={id}
                        name={name}
                        label={label}
                        type={type}
                        multiline={multiline}
                        minRows={minRows}
                        InputLabelProps={{ sx: { fontSize: 16 } }}
                        InputProps={{ sx: { fontSize: 20 } }}
                        sx={{ my: 3 }}
                        variant='standard'
                        value={forms[id]}
                        error={!!errors[id]}
                        helperText={errors[id] || ''}
                        onChange={e => {
                          setForms({
                            ...forms,
                            [id]: e.target.value
                          });
                          validate();
                        }}
                        inputRef={refInputs[id]}
                        required
                        fullWidth
                      />
                    )
                  )}
                  <Button
                    type='submit'
                    disabled={disabled}
                    variant='contained'
                    size='large'
                    sx={{
                      color: '#fff'
                    }}
                    fullWidth
                    onClick={() => {
                      if (validate()) {
                        // console.log('ok');
                        // setDisabled(true);
                      } else {
                        alert(
                          ''
                        );
                      }
                    }}
                  >
                    Send
                  </Button>
                  <Button
                    variant='text'
                    fullWidth
                    sx={{ mt: 4 }}
                    onClick={() => {
                      setForms(clearForms());
                      setErrors(clearForms());
                      setDisabled(true);
                    }}
                  >
                    Clear
                  </Button>
                </form>
                <iframe
                  title='iframe'
                  name='onSubmitIframe'
                  style={{ display: 'none' }}
                  onLoad={() => {
                    setIsSubmit(true);
                  }}
                />
              </Box>
            )}
          </Grid>
        </Grid>
      </Box>
    </Layout>
  );
};

export default Page;

export const Head = () => <Seo title='CONTACT' />;
